define(function () {
  // Romanian
  return {
    errorLoading: function () {
      return 'Rezultatele nu au putut fi incrcate.';
    },
    inputTooLong: function (args) {
      var overChars = args.input.length - args.maximum;

      var message = 'V rugm s tergei' + overChars + ' caracter';

      if (overChars !== 1) {
        message += 'e';
      }

      return message;
    },
    inputTooShort: function (args) {
      var remainingChars = args.minimum - args.input.length;

      var message = 'V rugm s introducei ' + remainingChars +
        ' sau mai multe caractere';

      return message;
    },
    loadingMore: function () {
      return 'Se ncarc mai multe rezultate';
    },
    maximumSelected: function (args) {
      var message = 'Avei voie s selectai cel mult ' + args.maximum;
      message += ' element';

      if (args.maximum !== 1) {
        message += 'e';
      }

      return message;
    },
    noResults: function () {
      return 'Nu au fost gsite rezultate';
    },
    searching: function () {
      return 'Cutare';
    },
    removeAllItems: function () {
      return 'Eliminai toate elementele';
    }
  };
});
