define(function () {
  // French
  return {
    errorLoading: function () {
      return 'Les rsultats ne peuvent pas tre chargs.';
    },
    inputTooLong: function (args) {
      var overChars = args.input.length - args.maximum;

      return 'Supprimez ' + overChars + ' caractre' +
        ((overChars > 1) ? 's' : '');
    },
    inputTooShort: function (args) {
      var remainingChars = args.minimum - args.input.length;

      return 'Saisissez au moins ' + remainingChars + ' caractre' +
        ((remainingChars > 1) ? 's' : '');
    },
    loadingMore: function () {
      return 'Chargement de rsultats supplmentaires';
    },
    maximumSelected: function (args) {
      return 'Vous pouvez seulement slectionner ' + args.maximum +
        ' lment' + ((args.maximum > 1) ? 's' : '');
    },
    noResults: function () {
      return 'Aucun rsultat trouv';
    },
    searching: function () {
      return 'Recherche en cours';
    },
    removeAllItems: function () {
      return 'Supprimer tous les lments';
    }
  };
});
